import apiService from "@/services/apiService.js";
import Alert from "@/components/UI/Alert.vue";
import { mapState } from "vuex";
export default {
  components: {
    Alert
  },
  data() {
    return {
      dialog: false
    };
  },
  async fetch({ store, error, params }) {
    try {
      await store.dispatch("fetchUser", params.id);
    } catch (error) {}
  },
  computed: mapState({
    // state => state.bootcamp
    fetchedUser: ["fetchedUser"],
    user: ["user"],
    rightTo() {
      if (this.fetchedUser._id === this.user._id) {
        return true;
      }
    }
  }),
  methods: {
    compDialog() {
      this.dialog = true;
    },
    no() {
      this.dialog = false;
    },
    yes() {
      this.$store.dispatch("deleteUser", this.fetchedUser._id).then(() => {
        this.$store.dispatch("loadingON").then(() => {
          this.$store.dispatch("logout").then(() => {
            this.$store.dispatch("loadingOFF");
            this.$router.push("/");
          });
        });
      });
      this.dialog = false;
    },
    updateUser() {
      this.$router.push("/editUser/" + this.fetchedUser._id);
    },
    initials(name) {
      const init = name
        .split(" ")
        .map(n => n.charAt(0).toUpperCase())
        .join("");
      return init;
    }
  }
};
